import React, { useState, useEffect } from 'react'
import { Link } from "gatsby"
import * as Scroll from 'react-scroll'
import { useLocation } from "@reach/router"
import * as style from "../styles/common.module.scss"


const Menu = () => {

    const location = useLocation()
    const rootPath = `${__PATH_PREFIX__}/`
    const isRootPath = location.pathname === rootPath

    const [active, setActive] = useState(false)
    const classToggle = () => {
      setActive(!active)
    }
    
    return (
        
        <menu className={style.menuWrapper}>

          <button onClick={classToggle} className={`${style.menuBtn} ${(active ? style.active : '')}`}></button>

          <div className={`${style.menuInner} ${(active ? style.menuShow : '')}`}>
            <ul>
              <li><Link to="/"></Link></li>
              <li><span>()</span></li>
              <li className={style.subMenu}><Link to="/policy"></Link></li>
              <li className={style.subMenu}><Link to="/vice-policy"></Link></li>
              <li className={style.subMenu}><Link to="/officers"></Link></li>
              <li className={style.subMenu}><Link to="/sougoukeikaku2021">2021</Link></li>
              <li className={style.subMenu}><Link to="/history"></Link></li>
              <li><span>()</span></li>
              <li className={style.subMenu}><Link to="/news/1" onClick={classToggle}  className={style.off}></Link></li>
              <li className={style.subMenu}><Link to="/past/1" onClick={classToggle} ></Link></li>
              <li className={style.subMenu}><Link to="/syokan/1" onClick={classToggle}></Link></li>
              <li><Link to="/joinus"></Link></li>
              <li><Link to="/senior"></Link></li>
              <li><Link to="/contact"></Link></li>
              <li><Link to="/privacypolicy"></Link></li>
              <li><Link to="/socialmediapolicy"></Link></li>
              <li><a href="https://drive.google.com/file/d/1trmTn_ztmXB-9B2aEI5B2cy79z69yuGi/view" target="_blank"></a></li>
              <li><Link to="/regulations"></Link></li>
              <li><a href="https://www.jaycee.or.jp/" target="_blank"></a></li>
              <li><a href="https://www.jaycee.or.jp/kanto" target="_blank"><br/></a></li>
              <li><a href="https://www.jaycee.or.jp/kanto/yamanashi" target="_blank"><br/></a></li>
           </ul>
         </div> 
       </menu>
    )
}

export default Menu